.backdrop {
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(196, 183, 112, 0.2);

  width: 100%;
  height: 100%;
  z-index: 1000;
  overflow: scroll;

  padding: 40px;
  opacity: 1;

  transition: opacity $timing-cubic;

  &.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
}

.modal__btn {
  position: absolute;
  top: 5px;
  right: 5px;

  border: none;
  background-color: transparent;
  z-index: 50;
  
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  padding: 15px;
  padding-top: 50px;
  margin-left: auto;
  margin-right: auto;

  background-color: rgb(252, 239, 165);
  text-align: left;

  width: 300px;
  height: 595px;

  @media screen and (min-width: 480px) {
    width: 400px;
  }

  @media screen and (min-width: 768px) {
    width: 700px;
    padding-left: 40px;
    padding-right: 40px;
  }
  //
}

.modal__pictures-img {
  width: 50px;
  height: 50px;
}

.modal__pictures-list {
  display: flex;
  flex-direction: column;
  // gap: 30px;
  background-color: #daf155;
}

.modal__pictures-item {
  padding-top: 15px;
  padding-bottom: 15px;
}

.modal__pictures-item:nth-child(2n) {
  background-color: #d3f08a;
}

/**
  |============================
  | modal form
  |============================
*/
.order-form {
  position: absolute;
  top: 0;
  right: 0;
  padding: 15px;
  width: 100%;
  text-align: center;
  padding-left: 20%;
}

.order-form__title {
  display: block;
  text-align: center;

  text-transform: uppercase;
  color: $color-text-primary;
  margin-bottom: 10px;
  // transform: translateX(50%);
}

.modal-form__label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 30px;

  @media screen and (min-width: 768px) {
    padding-left: 50px;
    padding-right: 50px;
  }
}

.modal-form__input {
  margin-right: 0;
  width: 100px;
  @media screen and (min-width: 768px) {
    width: 100px;
  }
  padding: 12px;

  color: $color-text-primary;
  @include fonts(12px, 18px, 400);

  border: 1px solid #ffe550;
  border-radius: 30px;
  outline: transparent;

  @media screen and (min-width: $tablet) {
    margin-right: 22px;
    max-width: 395px;
  }

  @media screen and (min-width: $desktop) {
    margin-right: 32px;
  }

  &::placeholder {
    color: $color-form-placeholder;
    @include fonts(12px, 18px, 400);
  }

  &:valid:not(:placeholder-shown) {
    border-color: $color-btn-border;
  }

  &:invalid:not(:placeholder-shown) {
    border-color: $color-form-error;
  }

  &:focus-within {
    border-color: $color-btn-border-hover;
  }
}
